<template lang="pug">
    div.withdrawals-conent
        div.ticket-conent
            div.total-ticket
                label.total 总{{appInit.ticket_name}}
                div.count(v-text="user.can_use_ticket")
            div.day-ticket
                label.day 今日可领取{{appInit.ticket_name}}
                div.count(v-text="user.day_ticket_max")
        div.input-box 
            label 提现{{appInit.ticket_name}}
            input(type="number" v-model="ticket" )
            i.close.icon.iconfont(v-if="ticket" @click="close") &#xe612;
        div.to-show(v-if="ticket" v-text="`${(ticket*user.ratio).toFixed(2)}元`") 
        div.to-show(v-else) 
        div.btn-group
            div.btn-confirm(@click="submit()") 确认
</template>
<script>
import queryString from 'querystring'
import { Toast } from 'mint-ui'
import { mapGetters } from 'vuex'
export default {
    name: 'profitWithdrawals',
    data() {
        return {
            user: '',
            ticket: '',

        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_money_carry_alipay' : this.api.userMoneyCarryAlipay();
            this.axios.get(axiosUrl).then(res => {
                if (res.data.status == 1) {
                    this.user = res.data
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    Toast(err || '请求失败')
                });
        },
        check() {
            if (!$.trim(this.ticket)) {
                Toast('请输入提现秀票数量');
                return false;
            }
            else {
                return true;
            }
        },
        submit() {
            if (this.check()) {
                let axiosUrl = this.api.userSubmitRefundAlipay();
                this.axios.post(axiosUrl, queryString.stringify({
                    ticket: this.ticket,
                })).then(res => {
                    if (res.data.status == 1) {
                        Toast(res.data.error || '操作成功');
                        this.$router.push('/user/profit');
                    }
                    else {
                        Toast(res.data.error || '操作失败');
                    }
                })
                    .catch(err => {
                        Toast(err || '网络异常，请求失败')
                    });
            }

        },
        // 清空输入框
        close() {
            this.ticket = '';
        }
    },

    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        })
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.withdrawals-conent {
    padding: .5rem;
    margin-top: .75rem;
    .ticket-conent {
        display: flex;
        justify-content: space-between;
        .total-ticket {
            label {
                color: @fc-dark;
            }
            .count {
                font-size: .8rem;
                color: @fc-main;
            }
        }
        .day-ticket {
            text-align: center;
            .count {
                font-size: .8rem;
                color: @fc-main;
            }
        }
    }
    .input-box {
        display: flex;
        align-items: center;
        white-space: nowrap;
        margin-top: .75rem;
        position: relative;
        input {
            height: 2rem;
            width: 100%;
            background: #fff;
            border: none;
            border-radius: .5rem;
            outline: none;
            padding: 0 1.5rem 0 .5rem;
        }
        label {
            color: @fc-dark;
            margin-right: .5rem;
        }
        .close {
            position: absolute;
            right: .5rem;
            color: @fc-light;
        }
    }
    .to-show {
        height: 1rem;
        text-align: right;
        color: @color-theme;
    }
    .btn-group {
        margin-top: 1rem;
        padding: 0 .5rem;
        .btn-confirm {
            height: 2.15rem;
            text-align: center;
            line-height: 2.15rem;
            background-color: @color-theme;
            color: #fff;
            font-size: .9rem;
            border-radius: 1rem;
        }
    }
}
</style>




